import { z } from 'zod';

export const RequestPreviewSchema = z.object({
    service_type: z.string().min(1, 'This field is required').optional(),
    purpose: z.string().min(1, 'This field is required').optional(),
    time: z.string().min(1, 'This field is required').optional(),
    date: z.date({
        required_error: 'This field is required',
    }),
    note: z.string().min(1, 'This field is required').optional(),
    // attachment: z.instanceof(File, { message: 'File is required' }).optional(),
    reject_reason: z.string().optional(),
    reject_attachment: z.instanceof(File, { message: 'File is required' }).optional(),
});

export type RequestPreviewFormValues = z.infer<typeof RequestPreviewSchema>;
